import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import "../styles/main.scss";
class Main extends Component {
    render() {
        return (
            <div className="app-main">
                <label>
                    <div class="remind">Click To Switch Day/Night!</div>
                    <input type="checkbox" />
                    <div className="scene">
                        <div class="sun"></div>
                        <div class="moon"></div>
                        <div class="lefttopText">
                            <div class="blueblock"></div>
                            <h5>
                                A DAY
                                <br />
                                ON THE ISLAND
                            </h5>
                            <h1>
                                Aegean Sea
                                <br />
                                <span class="nightText">Night Sky</span>
                                <span class="dayText">Day Sky</span>
                            </h1>
                            <p>pajir.github.io</p>
                        </div>
                        <div class="allstars">
                            <div class="star1"> </div>
                            <div class="star2"> </div>
                            <div class="star3"> </div>
                            <div class="star4"> </div>
                            <div class="star5"> </div>
                            <div class="star6"> </div>
                            <div class="star7"> </div>
                            <div class="star8"> </div>
                            <div class="star9"> </div>
                            <div class="star10"> </div>
                            <div class="star11"> </div>
                            <div class="star12"> </div>
                            <div class="star13"> </div>
                            <div class="star14"> </div>
                            <div class="star15"> </div>
                            <div class="star16"> </div>
                            <div class="star17"> </div>
                            <div class="star18"> </div>
                            <div class="star19"> </div>
                            <div class="star20"> </div>
                            <div class="star21"> </div>
                            <div class="star22"> </div>
                            <div class="star23"> </div>
                            <div class="star24"> </div>
                            <div class="star25"> </div>
                            <div class="star26"> </div>
                            <div class="star27"> </div>
                            <div class="star28"> </div>
                            <div class="star29"> </div>
                            <div class="star30"> </div>
                        </div>
                        <div class="building">
                            <div class="house1">
                                <div class="window">
                                    <div class="circle"></div>
                                    <div class="circle"></div>
                                    <div class="circle"></div>
                                </div>
                            </div>
                            <div class="house2">
                                <div class="door"></div>
                            </div>
                            <div class="house3">
                                <div class="wall">
                                    <div class="window"></div>
                                </div>
                                <div class="wall">
                                    <div class="window"></div>
                                </div>
                                <div class="wall">
                                    <div class="window"></div>
                                </div>
                                <div class="roof">
                                    <div class="rooftop">
                                        <div class="cross"></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </label>
            </div>
        );
    }
}

export default withRouter(Main);
